import Text from '../../../component/Text'
import styles from './index.less'

const TextDemo = () => {
    return (
        <div>
        <Text level="h1" className={styles.line}>主标题，级别 h1,字号 24px</Text>
        <Text level="h2" className={styles.line}>普通标题，级别 h2，字号 18px</Text>
        <Text level="h3" className={styles.line}>普通标题，级别 h3，字号 16px</Text>
        <Text level="importent" className={styles.line}>突出内容，级别 importent，字号 14px</Text>
        <Text level="text" className={styles.line}>正文，级别 text，字号 13px</Text>
        <Text level="tips" className={styles.line}>说明文字，级别 tips,字号 12px</Text>
        <br />
        <Text ellipsis className={styles.ellipsis}>文字缩略，文字很长很长很长很长很长</Text>
        <Text pre className={styles.line}>{'显示文字换行\n第二行\n第三行'}</Text>
    </div>
    )
}


export default TextDemo
