/* eslint-disable import/no-webpack-loader-syntax */

import Text from '../../component/Text'
import styles from './index.less'
import Demo from '../../component/Demo'
import TextDemoTsx from '!!raw-loader!./TextDemo/index.tsx'
import TextDemoLess from '!!raw-loader!./TextDemo/index.less'
import TextDemo from './TextDemo'

const TextView = () => {

    return (
        <div>
            <section>
                <Text level="h1" className={styles.title}>文字</Text>
                <Text>UI 字体系统基于微软雅黑（ Microsoft Yahei ），提供样式文件和组件两种方案：</Text>
            </section>
            <section>
                <Text level="h1" className={styles.title}>使用组件</Text>
                <Text>UI 提供了 Text 组件以供使用： </Text>
            </section>
            <Demo 
            title="多种字体"
            demo={<TextDemo />}
            codes={
                [
                    {name: 'index.tsx', type: 'tsx', code: TextDemoTsx},
                    {name: 'index.less', type: 'less', code: TextDemoLess}
                ]
            }
            
            />
        </div>
    )
}


export default TextView;
